/* 상단영역 //////////////////////////////////////////////// */

.t11 {
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 2px solid #000;
}

.t11 img {
    width: 120%;
    color: #fff;
    cursor: pointer;
}

.hom2 {
    width: 80px;
}

.t22 {
    display: flex;
    align-items: center;
    justify-content: center;
}

.t22 h1 {
    font-size: 0.9vw;
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

.t22 h1:after {
    display: block;
    content: "";
    border-bottom: solid 2px #000;
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
}

.t22 h1:hover:after {
    transform: scaleX(1);
}

.br2 {
    border-right: 2px solid #000;
}

/* 메인영역 //////////////////////////////////////////////// */

